.form__control {
    display: flex;
    align-items: center;
    position: relative;
    font-size: 14px;
}

.form__control.error input, 
.form__control.error textarea {
    border: 1px solid #ff0000 ;
}

.form__control textarea {
    width: 100%;
    resize: none;
    outline: none;
    padding-top: 8px;
}

.form__error {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    padding-right: 10px;
    color: #ff0000;
}



